import { HttpParams } from "@angular/common/http";
import { Component, OnInit, ViewChild } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  AbstractControl,
  FormArray,
  FormControl,
} from "@angular/forms";
import { DateAdapter } from "@angular/material/core";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router, ActivatedRoute } from "@angular/router";
import { BehaviorSubject, Subject, Subscription, takeUntil } from "rxjs";
import { AccountService } from "src/app/account/data/services/account.service";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { CcentreLookupComponent } from "src/app/erp-finance/data/lookups/ccentre-lookup/ccentre-lookup.component";
import { ExpCCentreLookupComponent } from "src/app/erp-finance/data/lookups/exp-c-centre-lookup/exp-c-centre-lookup.component";
import { ExpenseLookupComponent } from "src/app/erp-finance/data/lookups/expense-lookup/expense-lookup.component";
import { GlsLookupComponent } from "src/app/erp-finance/data/lookups/gls-lookup/gls-lookup.component";
import { MaintainedCurrenciesLookUpComponent } from "src/app/erp-finance/data/lookups/maintained-currencies-look-up/maintained-currencies-look-up.component";
import { GlAccountsService } from "src/app/erp-finance/data/services/gl-accounts.service";
import { ImprestManagementService } from "src/app/erp-imprest/data/services/imprest-management.service";
import { ImprestValidationComponent } from "src/app/erp-imprest/data/validation/imprest-validation/imprest-validation.component";
import { ValidateStoreRequisitionComponent } from "src/app/erp-inventory/data/lookups/dialogs/validate-store-requisition/validate-store-requisition.component";
import { BrancstoresManagementService } from "src/app/erp-inventory/data/services/brancstores-management.service";
import { StoreRequisitionsServiceService } from "src/app/erp-inventory/data/services/store-requisitions-service.service";
import { AccessControlService } from "src/app/erp-procurement/data/services/_AccessControlService.service";
import {
  FilesService,
  SelectedFiles,
} from "src/app/shared/services/files.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";

import * as moment from "moment";
import { MatDatepickerInputEvent } from "@angular/material/datepicker";

@Component({
  selector: "app-manage-imprest",
  templateUrl: "./manage-imprest.component.html",
  styleUrls: ["./manage-imprest.component.scss"],
})
export class ManageImprestComponent implements OnInit {
  showForm = false;
  isLoading = false;
  pageFunction = "Add";
  mngForm: FormGroup;
  currentUser: any;
  currentUserCode: any;
  formData: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;
  hideSubmit = false;
  hideApprovals: boolean = true;
  approvalVisible: boolean = false;
  disableActions = false;
  requestCode: any;

  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  imprestAccountingStatuses: any[] = [
    { name: "Full" },
    { name: "Partial" },
    { name: "Return" },
  ];

  imprestTypes: any[] = [{ name: "Apply" }, { name: "Claim" }];

  minDate: Date;

  constructor(
    private fb: FormBuilder,
    private filesService: FilesService,
    private tokenStorageService: TokenStorageService,
    private branchStoreManService: BrancstoresManagementService,
    private imprestManService: ImprestManagementService,
    private snackbar: SnackbarService,
    private router: Router,
    private route: ActivatedRoute,
    private accessControlService: AccessControlService,
    private dialog: MatDialog,
    private dateAdapter: DateAdapter<Date>,
    private accountService: GlAccountsService
  ) {
    this.currentUser = this.tokenStorageService.getUser();
    this.currentUserCode = this.tokenStorageService.getUser().empPfNo;

    this.minDate = this.dateAdapter.today();

    this.minDate.setDate(this.minDate.getDate() + 1);
  }
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  ngOnInit(): void {
    this.getPage();

    if (
      this.route.queryParams &&
      typeof this.route.queryParams.subscribe === "function"
    ) {
      this.route.queryParams.subscribe((params) => {
        console.log("params: ", params);
        if (params.requestCode) {
          this.requestCode = params.requestCode;
          this.getDataById();
          this.pageAction = params.action;
        }
      });
      this.showForm = true;
    } else {
      this.showForm = true;
    }
  }

  pageAction = "Add";

  getDataById() {
    const params = new HttpParams().set("requestCode", this.requestCode);
    this.imprestManService
      .getImprestByRequestCode(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          if (res.entity) {
            //in thsi res, I cant access anything beyond res.entity. Why???
            this.formData = res.entity;
            console.log("this.formData: ", this.formData);
            if (this.pageAction === "Update") {
              this.pageFunction = "Update";
            } else if (this.pageAction === "Account") {
              this.pageFunction = "Account";
            } else if (this.pageAction === "View") {
              this.pageFunction = "View";
              this.disableActions = true;
              this.showForm = false;
            } else if (this.pageAction === "Validate") {
              this.pageFunction = "Validate";
              this.disableActions = true;
              this.approvalVisible = true;
              this.showForm = false;
            }

            if (this.formData.imprestAttachments.length > 0) {
              this.isFileDataLoading = false;
              console.log(
                "this.formData.imprestAttachments: ",
                this.formData.imprestAttachments
              );
              this.formData.imprestAttachments.forEach((element) => {
                const newRow = this.fb.group({
                  file: [element.file],
                  fileName: [element.fileName],
                  fileType: [element.fileType],
                });
                this.rows.push(newRow);
              });
              this.updateView();
            }
            this.showForm = true;
            this.getPage();

            if (this.pageFunction === "Add") {
              this.imprestTypes = [
                { name: "Apply" },
                { name: "Claim" },
                { name: "Account" },
              ];

              let event = { value: "Account" };
              this.imprestTypeChange(event);
              this.mngForm.patchValue({
                amountRequested: this.formData.amountRequested,
                imprestAttachments: this.formData.imprestAttachments,
                refImprestCode: this.formData.requestCode,
              });
            }
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {},
      });
  }

  getPage(): void {
    if (this.pageFunction === "Add") {
      this.generateEmptyForm();
      this.hideApprovals = true;
    } else if (this.pageFunction === "Update") {
      this.generateFormWithData();
      this.hideApprovals = true;
    } else if (this.pageFunction === "Account") {
      this.generateEmptyForm();
      this.hideApprovals = true;
    } else if (this.pageFunction === "View") {
      this.generateDisabledFormWithData();
      this.hideSubmit = true;
      this.hideApprovals = true;
    } else if (this.pageFunction === "Validate") {
      this.generateDisabledFormWithData();
      this.hideApprovals = false;
      this.hideSubmit = true;
    }
  }

  //   - accountByDate
  // - expenseAc
  // - expenseCode

  generateEmptyForm() {
    this.mngForm = this.fb.group({
      id: [""],
      imprestType: [""],
      imprestTitle: ["", [Validators.required]],
      description: ["", [Validators.required, Validators.maxLength(300)]],
      amountRequested: [
        "",
        [Validators.required, Validators.pattern(/^\d+(\.\d{1,2})?$/)],
      ],
      currency: ["KES"],
      costcentercode: [this.currentUser.costCenterCode, [Validators.required]],
      expenseAc: [""],
      expenseCode: [""],
      accountByDate: [""],
      employeeName: this.currentUser.username,
      employeeCode: this.currentUserCode,
      balance: [""],
      imprestAccountingStatus: [""],
      imprestAttachments: [[]],
      refImprestCode: [""],
    });

    // Subscribe to changes in imprestType control
    this.subscribeToChangesInImprestType();
  }

  generateFormWithData() {
    this.mngForm = this.fb.group({
      id: [this.formData.id],
      imprestType: [this.formData.imprestType],
      requestCode: [this.formData.requestCode],
      imprestTitle: [this.formData.imprestTitle, [Validators.required]],
      description: [
        this.formData.description,
        [Validators.required, Validators.maxLength(300)],
      ],
      amountRequested: [
        this.formData.amountRequested,
        [Validators.required, Validators.pattern(/^\d+(\.\d{1,2})?$/)],
      ],
      currency: [this.formData.currency],

      costcentercode: [this.formData.costcentercode, [Validators.required]],

      expenseAc: [this.formData.expenseAc, [Validators.required]],
      expenseCode: [this.formData.expenseCode, [Validators.required]],
      accountByDate: [this.formData.accountByDate, [Validators.required]],

      imprestAttachments: [this.formData.imprestAttachments],

      employeeCode: [this.formData.employeeCode],
      employeeName: [this.formData.employeeName],

      balance: [this.formData.balance],
      imprestAccountingStatus: [this.formData.imprestAccountingStatus],
      refImprestCode: [this.formData.refImprestCode],
    });
    this.subscribeToChangesInImprestType();

    let event = { value: this.mngForm.value };
    this.imprestTypeChange(event);
  }

  generateDisabledFormWithData() {
    this.mngForm = this.fb.group({
      imprestType: [this.formData.imprestType],
      imprestTitle: [this.formData.imprestTitle, [Validators.required]],
      description: [this.formData.description, [Validators.required]],
      amountRequested: [
        this.formData.amountRequested,
        [Validators.required, Validators.pattern(/^\d+(\.\d{1,2})?$/)],
      ],
      currency: [this.formData.currency, [Validators.required]],

      costcentercode: [this.formData.costcentercode, [Validators.required]],

      expenseAc: [this.formData.expenseAc, [Validators.required]],
      expenseCode: [this.formData.expenseCode, [Validators.required]],
      accountByDate: [this.formData.accountByDate, [Validators.required]],

      imprestAttachments: [this.formData.imprestAttachments],

      employeeCode: [this.formData.employeeCode],
      employeeName: [this.formData.employeeName],
    });
  }
  selectedDate: any;
  onDateChange(event: MatDatepickerInputEvent<Date>) {
    const selectedDate = event.value;
    const selectedDateUTC = selectedDate.toISOString();

    console.log("event.value:: ", event.value);

    console.log("selectedDateUTC:: ", selectedDateUTC);

    // this.mngForm.patchValue({
    //   accountByDate: selectedDateUTC
    // })
  }
  imprestTypeChange(event: any) {
    console.log("Event: ", event.value);
    this.toggleValidators(event.value);

    if (event.value === "Apply") {
      this.mngForm.patchValue({
        imprestType: event.value,
        imprestTitle: "Advance Request",
        expenseCode: "EXP5",
        expenseAc: "106201",
      });
      this.hideNonAdvanceFields = false;
    } else if (event.value === "Claim") {
      this.mngForm.patchValue({
        imprestType: event.value,
        imprestTitle: "Expense Request",
        expenseCode: "",
        expenseAc: "",
      });
      this.hideNonAdvanceFields = true;
    } else if (event.value === "Account") {
      this.mngForm.patchValue({
        imprestType: event.value,
        imprestTitle: "Account for imprest",
        expenseCode: "",
        expenseAc: "",
      });
      this.hideNonAdvanceFields = true;
    }
  }

  hideNonAdvanceFields: boolean = false;
  subscribeToChangesInImprestType() {
    this.mngForm.get("imprestType").valueChanges.subscribe((imprestType) => {
      if (imprestType === "Apply") {
        // Set expenseAc, expenseCode, and accountByDate as required
        this.mngForm.get("expenseAc").setValidators([Validators.required]);
        this.mngForm.get("expenseCode").setValidators([Validators.required]);
        this.mngForm.get("accountByDate").setValidators([Validators.required]);
        this.hideNonAdvanceFields = true;
      } else {
        // Clear validators if imprestType is not 'Claim'
        this.mngForm.get("expenseAc").clearValidators();
        this.mngForm.get("expenseCode").clearValidators();
        this.mngForm.get("accountByDate").clearValidators();
      }
      // Update the validity of the form controls after changing validators
      this.mngForm.get("expenseAc").updateValueAndValidity();
      this.mngForm.get("expenseCode").updateValueAndValidity();
      this.mngForm.get("accountByDate").updateValueAndValidity();

      this.hideNonAdvanceFields = false;
    });
  }
  toggleValidators(imprestType) {
    if (imprestType === "Apply") {
      // Set expenseAc, expenseCode, and accountByDate as required
      this.mngForm.get("expenseAc").setValidators([Validators.required]);
      this.mngForm.get("expenseCode").setValidators([Validators.required]);
      this.mngForm.get("accountByDate").setValidators([Validators.required]);
      this.hideNonAdvanceFields = true;
    } else {
      // Clear validators if imprestType is not 'Claim'
      this.mngForm.get("expenseAc").clearValidators();
      this.mngForm.get("expenseCode").clearValidators();
      this.mngForm.get("accountByDate").clearValidators();
    }
    // Update the validity of the form controls after changing validators
    this.mngForm.get("expenseAc").updateValueAndValidity();
    this.mngForm.get("expenseCode").updateValueAndValidity();
    this.mngForm.get("accountByDate").updateValueAndValidity();

    this.hideNonAdvanceFields = false;
  }
  //*************************************************************************************************************************************** */

  //*************************************************************************************************************************************** */
  //Currencies LookUP
  selectedItem: any;
  _currencyLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "View Currency",
      selected: this.selectedItem,
    };
    const dialogRef = this.dialog.open(
      MaintainedCurrenciesLookUpComponent,
      dialogConfig
    );
    dialogRef.afterClosed().subscribe((res) => {
      res.data.forEach((element) => {
        this.mngForm.patchValue({
          currency: element.currencyCode,
        });
      });
    });
  }

  selectedCostCenter: any[] = [];
  costCenterLookUp() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "Single CostCenter",
      selected: this.selectedCostCenter,
    };

    const dialogRef = this.dialog.open(CcentreLookupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe((result) => {
      console.log("lookupresonse: ", result.data);

      this.mngForm.patchValue({
        costcentercode: result.data[0].costCenterCode,
      });

      if (this.mngForm.value.expenseCode) {
        this.getAccountNumberByExpAndCostCenter();
      }
    });
  }

  glSelectedItem: any[] = [];
  glsLookUp() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "Single GL Account",
      selected: this.glSelectedItem,
      type: "Expense",
    };
    const dialogRef = this.dialog.open(GlsLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((result) => {
      console.log("result:::", result);
      result.data.forEach((element) => {
        this.mngForm.patchValue({
          expenseAc: element.accountID,
        });
      });
    });
  }

  selectedExpense: any;
  expenseLookUp() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "Single Expense",
      selected: this.selectedExpense,
    };

    const dialogRef = this.dialog.open(ExpenseLookupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe((result) => {
      console.log("lookupresonse: ", result.data);

      this.mngForm.patchValue({
        expenseCode: result.data[0].expenseCode,
        // expenseAc: result.data[0].expenseGlAccount,
      });

      if (this.mngForm.value.costcentercode) {
        this.getAccountNumberByExpAndCostCenter();
      }
    });
  }

  // **************************************************************************************************
  //UploadDocuments

  attachementsDataSource = new BehaviorSubject<AbstractControl[]>([]);
  displayColumns = [
    "id",
    "fileName",
    "fileType",
    "selectFile",
    "download",
    "action",
  ];
  rows: FormArray = this.fb.array([]);
  documentsForm: FormGroup = this.fb.group({ filedetails: this.rows });

  isFileDataLoading: boolean = true;

  fileForm: FormGroup;
  fileInputs: FormArray;

  createFileForm() {
    this.fileForm = this.fb.group({
      files: this.fb.array([]),
    });
    //this.fileInputs = this.fileForm.get('files') as FormArray;
    this.addFileRow();
  }

  addFileRow() {
    this.isFileDataLoading = false;
    const row = this.fb.group({
      file: ["", Validators.required],
      fileType: ["", Validators.required],
      fileName: ["", Validators.required],
    });
    //this.fileInputs.push(row);
    this.rows.push(row);
    this.attachementsDataSource.next(this.rows.controls);
  }

  updateView() {
    this.attachementsDataSource.next(this.rows.controls);
    this.mngForm.patchValue({
      imprestAttachments: this.documentsForm.value.filedetails,
    });
  }

  // deleteCall(index){
  //   this.dataSource.splice(index, 1);
  // }

  deleteCall(row: AbstractControl) {
    const dataArray = this.attachementsDataSource.getValue(); // Get the underlying array
    const index = dataArray.indexOf(row); // Find the index of the row

    if (index !== -1) {
      dataArray.splice(index, 1); // Remove 1 item at the specified index
      this.attachementsDataSource.next(dataArray); // Update the BehaviorSubject with the modified array
    }
  }

  public selectedFiles: SelectedFiles[] = [];
  public isFileLoading = new BehaviorSubject(false);

  currFile: string;
  currfileName: any;
  currType: any;

  onSelectFile(files, selectedRow, index) {
    console.log("row: ", selectedRow.value, index);
    this.isFileLoading.next(true);
    this.selectedFiles = [];
    this.filesService.toBase64(files, this.selectedFiles).subscribe((res) => {
      if (res) {
        this.isFileLoading.next(false);

        this.selectedFiles = res;
        console.log("selectedFiles: ", this.selectedFiles);

        console.log("this.selectedFiles[0].name: ", this.selectedFiles[0].name);

        this.currFile = this.selectedFiles[0].base64;

        const fileName = this.selectedFiles[0].name;
        const fileParts = fileName.split(".");
        const name = fileParts.slice(0, -1).join("."); // Join all parts except the last one
        const extension = fileParts[fileParts.length - 1];

        console.log("Name:", name); // Output: Lease Armotization (3)
        console.log("Extension:", extension); // Output: xlsx

        this.currfileName = name;
        this.currType = extension;

        this.rows.at(index).patchValue({
          file: this.currFile,
          fileName: this.currfileName,
          fileType: this.currType,
        });
        this.updateView();
      }
    });
  }

  downloadDocument(row: any) {
    let file = row.value.file;
    let fileName = row.value.fileName;
    let fileType = row.value.fileType;

    // Download PDF in Chrome etc.
    const source = `${file}`;
    const link = document.createElement("a");
    link.href = source;
    link.download = `${fileName}.${fileType}`;
    link.click();
  }

  // **************************************************************************************************
  getAccountNumberByExpAndCostCenter() {
    const params = new HttpParams()
      .set("costCenterCode", this.mngForm.value.costcentercode)
      .set("expenseCode", this.mngForm.value.expenseCode);

    console.log("params:: ", params);

    this.accountService
      .getGLAccountByExpAndCostCenter(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("resres:: ", res);

          if (res.entity) {
            this.mngForm.patchValue({
              expenseAc: res.entity.glaccount,
            });
            this.snackbar.showNotification("snackbar-success", res.message);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {},
      }),
      Subscription;
  }

  //************************************************************************************************* */
  //Showing the submit button
  posting: boolean = false;

  onSubmit() {
    this.posting = true;
    console.log("this.mngForm.value: ", this.mngForm.value);
    if (this.pageFunction == "Add") {
      this.imprestManService
        .addImprest(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res: ", res);

            if (res.statusCode == 200) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate([
                "/erp-imprest/imprest-management/all-imprest",
              ]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    } else if (this.pageFunction == "Update") {
      this.imprestManService
        .updateImprest(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res.statusCode == 200) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate([
                "/erp-imprest/imprest-management/all-imprest",
              ]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    } else if (this.pageFunction == "Account") {
      this.imprestManService
        .addImprest(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res: ", res);

            if (res.statusCode == 200) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate([
                "/erp-imprest/imprest-management/all-imprest",
              ]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    }
  }

  cancel() {
    this.router.navigate(["/erp-imprest/imprest-management/all-imprest"]);
  }

  // **********************************************************************************************
  //validate branch requisition

  hasAccess: boolean;

  onValidate(userType) {
    let privilege = "";

    switch (userType) {
      case "HOD":
        privilege = "Imprest Validation";
        break;
      default:
        return;
    }

    this.hasAccess = this.accessControlService.hasPrivilege("ImprestModule", [
      privilege,
    ]);

    if (this.hasAccess) {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.autoFocus = true;
      dialogConfig.width = "650px";
      dialogConfig.data = {
        data: this.formData,
        userType: userType,
      };

      const dialogRef = this.dialog.open(
        ImprestValidationComponent,
        dialogConfig
      );

      dialogRef.afterClosed().subscribe((result) => {
        this.router.navigate(["/erp-imprest/imprest-management/all-imprest"]);
      });
    }
  }

  // *********************************************************************************************

  // **********************************************************************************************
}
