import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable, map } from "rxjs";
import { environment } from "src/environments/environment.prod";

@Injectable({
  providedIn: "root",
})
export class TransactionsService {
  constructor(private http: HttpClient) { }

  getTransactions(): Observable<any> {
    const TransactionsUrl = `${environment.baseUrlFinance}/api/v1/transactions/all`;
    return this.http.get<any[]>(TransactionsUrl);
  }
  getTransactionsByStatus(params: any): Observable<any> {
    const TransactionsUrl = `${environment.baseUrlFinance}/api/v1/transactions/all/by/fetchbyStatus`;
    return this.http.get<any>(TransactionsUrl, { params });
  }

  getTransactionsByRefCodeAndTransactionType(params: any): Observable<any> {
    const TransactionsUrl = `${environment.baseUrlFinance}/api/v1/transactions/find/by/refcodeandtransactiontype`;
    return this.http.get<any>(TransactionsUrl, { params });
  }
  getTransactionsByTranCode(params: any): Observable<any> {
    const TransactionsUrl = `${environment.baseUrlFinance}/api/v1/transactions/find/by/trancode`;
    return this.http.get<any>(TransactionsUrl, { params });
  }
  getTransactionsByStatusTransactionType(params: any): Observable<any> {
    const TransactionsUrl = `${environment.baseUrlFinance}/api/v1/transactions/find/by/statusandtransactiontype`;
    return this.http.get<any>(TransactionsUrl, { params });
  }

  getTransactionsById(params: any): Observable<any> {
    const TransactionsUrl = `${environment.baseUrlFinance}/api/v1/transactions/find/by/id`;

    return this.http.get<any>(TransactionsUrl, { params });
  }

  addTransactions(data: any): Observable<any> {
    const TransactionsUrl = `${environment.baseUrlFinance}/api/v1/transactions/add`;
    return this.http.post<any>(TransactionsUrl, data);
  }

  updateTransactions(TransactionsDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlFinance}/api/v1/transactions/update`;
    return this.http.put<any>(updateBillUrl, TransactionsDetails);
  }

  deleteTransactionsTemporarily(params: any): Observable<any> {
    const TransactionsUrl = `${environment.baseUrlFinance}/api/v1/transactions/delete`;
    return this.http.delete<any>(TransactionsUrl, { params });
  }


  validateTransactions(params: any, dataToValidate: any): Observable<any> {
    const TransactionsUrl = `${environment.baseUrlFinance}/api/v1/transactions/update/state`;
    return this.http.put<any>(TransactionsUrl, dataToValidate, {params});
  }

  // ********************************************************************************************************************

  updateAccountingTransactionDetailsStatus(params): Observable<any> {
    const Url = `${environment.baseUrlFinance}/api/v1/transactions/update/record/on/transaction`;
    return this.http.put<any>(Url, {}, { params });
  }

  updateAccountingTransactionDetailsForImprestStatus(params): Observable<any> {
    const Url = `${environment.baseUrlFinance}/api/v1/transactions/update/record/on/imprest/transaction`;
    return this.http.put<any>(Url, {}, { params });
  }

  
  //************************************************************************************************************************ */
}
