import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllImprestComponent } from './pages/imprest-request/all-imprest/all-imprest.component';
import { ManageImprestComponent } from './pages/imprest-request/manage-imprest/manage-imprest.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';

const routes: Routes = [
//Tax Parameters
{
  path: "all-imprest",
  component: AllImprestComponent,
  canActivate: [RoutePrivilegeGuard],
  data: { clientName: 'ImprestModule', requiredPrivilege: ["All Imprest"] },
},

{
  path: "manage-imprest",
  component: ManageImprestComponent,
  canActivate: [RoutePrivilegeGuard],
  data: { clientName: 'ImprestModule', requiredPrivilege: ["Manage Imprest"] },
},

];



@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ImprestRoutingModule { }
